@media screen and (min-width: 1024px) {
        
    .cheaclo-container {
        margin-left: 12em;
        text-align: justify;
    }

    .cheaclo-container > h1 {
        font-size: 3em;
    }

    .cheaclo-container > .chapter {
        display: flex;
        justify-content: space-between;
        margin-bottom: 4em;
    }

    .cheaclo-container > .chapter > .content {
        margin-right: 2em;
        width: 60%;
    }

    .cheaclo-container > .chapter > .content > p {
        margin-bottom: 2em;
    }

    .cheaclo-container > .chapter > .content > video {
        width: 100%;
        margin-bottom: 2em;
    }

    .cheaclo-container > .chapter > .content > p > a {
        text-decoration: none;
        color: #C5BDD8;
        margin-left: 0.5em;
    }

}